/* General styles for the register page */
.register-page {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh; /* Full viewport height */
  background-image: url('C:\Users\Toquir Alam\Desktop\Expense-Management-System\client\src\images\money.jpg'); /* Background image */
  background-size: cover; /* Cover the entire viewport */
  background-position: center; /* Center the background image */
  font-family: 'Arial', sans-serif; /* Font style */
}

/* Form container */
.register-page .ant-form {
  background-color: rgba(255, 255, 255, 0.9); /* White background with slight transparency */
  padding: 30px; /* Increased padding */
  border-radius: 10px; /* More rounded corners */
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2); /* Enhanced shadow */
  width: 350px; /* Slightly wider form */
}

/* Form title */
.register-page h1 {
  text-align: center; /* Centered title */
  margin-bottom: 20px; /* Space below title */
  color: #007bff; /* Primary color for the title */
  font-size: 28px; /* Larger font size */
  font-weight: bold; /* Bold font */
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2); /* Subtle shadow for depth */
}

/* Input fields */
.register-page .ant-input {
  width: 100%; /* Full width */
  padding: 12px; /* Increased padding */
  margin: 10px 0; /* Space between inputs */
  border: 1px solid #ced4da; /* Light border */
  border-radius: 5px; /* Rounded corners */
  font-size: 16px; /* Font size */
  transition: border-color 0.3s; /* Smooth transition */
}

/* Input focus effect */
.register-page .ant-input:focus {
  border-color: #007bff; /* Change border color on focus */
  box-shadow: 0 0 5px rgba(0, 123, 255, 0.5); /* Subtle shadow on focus */
  outline: none; /* Remove default outline */
}

/* Button styles */
.register-page .btn {
  width: 100%; /* Full width */
  padding: 12px; /* Padding inside button */
  background-color: #007bff; /* Primary button color */
  color: #ffffff; /* White text color */
  border: none; /* No border */
  border-radius: 5px; /* Rounded corners */
  font-size: 16px; /* Font size */
  cursor: pointer; /* Pointer cursor on hover */
  transition: background-color 0.3s, transform 0.2s; /* Smooth transition */
}

/* Button hover effect */
.register-page .btn:hover {
  background-color: #0056b3; /* Darker shade on hover */
  transform: translateY(-2px); /* Slight lift effect */
}

/* Link styles */
.register-page .d-flex {
  display: flex;
  justify-content: space-between; /* Space between link and button */
  margin-top: 15px; /* Space above the link */
  font-size: 14px; /* Font size for the link */
}

/* Link hover effect */
.register-page .d-flex a {
  color: #007bff; /* Link color */
  text-decoration: none; /* Remove underline */
  transition: color 0.3s; /* Smooth transition */
}

.register-page .d-flex a:hover {
  color: #0056b3; /* Darker shade on hover */
}

/* Error message */
.register-page .error {
  color: red; /* Red color for error messages */
  font-size: 14px; /* Font size for error messages */
  margin-top: 10px; /* Space above error message */
  text-align: center; /* Center the error message */
}



