/* Analytics   */
/* Container Styling */
.container {
  max-width: 1200px; /* Set a max width for the container */
  margin: auto; /* Center the container */
  padding: 20px; /* Add padding around the container */
}

/* Cards in the grid */
.card {
  border: 1px solid #e0e0e0; /* Grey border */
  border-radius: 8px; /* Rounded corners */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Soft shadow */
  margin-bottom: 20px; /* Space between cards */
  transition: transform 0.3s; /* Smooth transform on hover */
  overflow: hidden; /* Handle overflowing content */
}

.card:hover {
  transform: translateY(-5px); /* Lift the card on hover */
}

/* Card Header */
.card-header {
  background-color: #f7f7f7; /* Light background */
  font-weight: bold; /* Bold text */
  padding: 15px; /* Add padding */
  text-align: center; /* Center align header text */
}

/* Card Body */
.card-body {
  padding: 5px; /* Side padding inside the card */
  text-align: center; /* Center align text */
}

/* Typography */
h5 {
  margin: 10px 0; /* Space between headings */
}

.text-success {
  color: green; /* Green for income */
}

.text-danger {
  color: red; /* Red for expense */
}

/* Progress Bars */
.ant-progress {
  width: 100%; /* Full width progress bar */
  margin-top: 10px; /* Space above progress bar */
}

/* Responsive design adjustments */
@media (min-width: 768px) {
  /* No need to set display flex; Bootstrap handles it */
}

/* General styling for headings */
h4 {
  margin-bottom: 20px; /* Space below headings */
}

/* Margin utility classes for Bootstrap */
.mt-3 {
  margin-top: 1rem; /* Margin top utility */
}

.m-3 {
  margin: 1rem; /* Margin utility for spacing */
}



.income-card {
  background-color: #e8f5e9; /* Light green background */
  border: 1px solid #4CAF50; /* Green border */
}

.turnover-card {
  background-color: #ffebee; /* Light red background */
  border: 1px solid #f44336; /* Red border */
}

.category-card {
  background-color: #f9fbe7; /* Light yellow background for categories */
  border: 1px solid #c0ca33; /* Darker yellow border */
  margin-top: 10px;
}
.text-center {
  text-align: center;
}

.small-card {
  font-size: 0.8em; /* Decrease font size */
  padding: 10px; /* Smaller padding */
  margin-bottom: 10px; /* Reduce margin */
}

.income-card, .turnover-card {
  width: 100%; /* Ensure they take less than full width */
  max-width: 700px; /* Set a max width for individual cards */
  padding: 5px; /* Reduce padding for a thinner appearance */
}

.card-body {
  padding: 8px; /* Adjust padding for better appearance */
}
.card-container {
  display: flex;
  flex-wrap: wrap; /* Allow wrapping */
  justify-content: space-around; /* Spacing around items */
  gap: 10px; /* Add some space between cards */
} 














